.search {
  max-width: 100%;
  height: 72px;
  background: #202020;
  border-bottom: 1px solid #424242;
  padding: 70px 0 69px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search__form {
  display: flex;
  align-items: center;
  width: 100%;
  height: 72px;
  border-radius: 9px;
  background: #2F2F2F;
}

.search__icon {
  display: block;
  width: 34px;
  height: 34px;
  margin: 19px 10px 19px 19px;
}

.search__input-label {
  display: inline-block;
  margin: 0;
}

.search__input {
  font-weight: 400;
  font-size: 18px;
  line-height: 20px;
  border: none;
  outline: none;
  width: 100%;
  height: 72px;
  color: #8b8b8b;
  background: #2f2f2f;
  padding: 0;
}

.search__input:focus {
  outline: 1px solid #4285F4;
}

.search__button {
  width: 58.65px;
  height: 34px;
  border-radius: 48px;
  background: #4285F4;
  color: #FFF;
  font-size: 11px;
  font-weight: 500;
  line-height: 18px;
  cursor: pointer;
  margin-right: 30px;
  margin-left: 10px;
  border: none;
  padding: 8px 10.35px;
}


.search-divider {
  display: block;
  width: 1px;
  height: 40px;
  background-color: #424242;
  margin-right: 29px;
}

.toggle-switch-label {
  display: flex;
  align-items: center;
}

.search__button:hover {
  transition: opacity 0.5s linear;
  opacity: 0.5;
}

.toggle-label {
  color: #FFF;
  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
  margin: 0 30px 0 14px;
}


@media screen and (max-width: 768px) {
  .search {
      padding: 70px 0 120px 0;
  }
}


@media screen and (max-width: 520px) {
  .search {
      height: 124px;
      padding: 56px 0 80px 0;
  }

  .search__form {
      position: relative;
  }

  .toggle-switch-label {
      position: absolute;
      top: 160%;
      left: 86px;
  }

  .search-divider {
      display: none;
  }

  .search__icon {
      display: none;
  }

  .search__input {
      margin-left: 19px;
  }

  .toggle-label {
      font-size: 11px;
      line-height: 18px;
  }

  .search__input {
      font-size: 14px;
  }
}
