.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 44px;
  width: 92%;
}

.navigation__reg {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.navigation__icon {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.navigation__links {
  display: flex;
  gap: 16px;
}

.navigation__link {
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
}

.navigation__link_active {
  font-weight: 500;
}

.navigation-nav {
  display: none;
}

.navigation__link_unregistered {
  font-size: 12px;
  line-height: 16px;
}

.navigation__link_active {
  font-weight: 500;
}

.navigation__button {
  width: 76px;
  height: 32px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  border-radius: 3px;
  border: none;
  background: #3ddc84;
  color: #000000;
  padding: 0;
  cursor: pointer;
  margin-left: 30px;
}

.navigation__link:hover,
.navigation__button:hover,
.navigation__btn:hover {
  transition: opacity 0.5s linear;
  opacity: 0.5;
}

@media screen and (max-width: 768px) {
  .navigation {
      justify-content: flex-end;
  }

  .navigation__button {
      margin-left: 30px;
  }

  .navigation__links {
      display: none;
  }

  .navigation-nav {
      display: block;
      padding: 0;
      width: 44px;
      height: 44px;
      border: none;
      cursor: pointer;
      background: transparent;
      background-size: contain;
      background-repeat: no-repeat;
      background-image: url('../../images/icon__burger.svg');
  }

  .navigation__icon {
      display: none;
  }
}

@media screen and (min-width: 320px) and (max-width: 380px) {

  .navigation__link {
      font-size: 10px;
      line-height: 16px;
  }

  .navigation__button {
      width: 54px;
      height: 26px;
      font-size: 10px;
      margin-left: 14px;
  }

  .navigation-nav {
      height: 43px;
      background-size: cover;
      margin-right: 10px;
  }
}
