.techs {
  background: #272727;
  max-width: 1140px;
  margin: auto;
  padding: 100px 70px;
}

.techs__title {
  color: #FFF;
  font-size: 22px;
  font-weight: 400;
  line-height: 27px;
  border-bottom: 1px solid #DADADA;
  margin: 0;
  padding-bottom: 23px;
  text-align: left;
}

.techs__conteiner {
  margin-bottom: 100px;
}

.techs__subtitle {
  color: #FFF;
  text-align: center;
  font-size: 50px;
  font-weight: 400;
  line-height: 58px;
  margin: 90px auto 26px;
}

.techs__text {
  max-width: 460px;
  color: #FFF;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin: 0 auto;
}

.techs__items {
  max-width: 690px;
  color: #FFF;
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(90px, 1fr));
  margin: 0 auto;
  gap: 10px;
}

.techs__item {
  border-radius: 10px;
  background: #303030;
  width: 90px;
  height: 60px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFF;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
}

@media screen and (max-width: 768px) {
  .techs {
      padding: 90px 50px;
  }

  .techs__conteiner {
      margin-bottom: 83px;
  }

  .techs__items {

      max-width: 658px;
      margin: 0 10px;
  }

  .techs__item {
      width: 84px;
      height: 57px;
  }

  .techs__subtitle {
      margin: 80px auto 22px;
  }

  .techs__text {
      max-width: 460px;
      font-size: 12px;
      line-height: 18px;
      margin: 0 auto;
  }
}

@media screen and (max-width: 370px) and (min-width: 320px) {
  .techs {
      background: #202020;

      padding: 70px 18px;
  }

  .techs__title {
      font-size: 18px;
      line-height: 22px;
      border-bottom: 1px solid #000;
      margin: 0;
      padding-bottom: 28px;
  }

  .techs__subtitle {
      font-size: 30px;
      line-height: 36px;
      margin: 60px auto 24px;
  }

  .techs__conteiner {
      margin-bottom: 50px;
  }

  .techs__text {
      font-size: 11px;
      line-height: 16px;
  }

  .techs__items {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(84px, 1fr));
      max-width: 178px;
      margin: 0 auto;
  }
}
