.card {
  width: 270px;
  display: flex;
  flex-direction: column;
  list-style-type: none;
}

.card__img {
  width: 100%;
  height: 151px;
}

.card-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 14px 0 20px 0;
}

.card-label {
  width: 226px;
  color: #FFF;
  font-size: 13px;
  font-weight: 500;
  line-height: 16px;
}

.card__button {
  width: 24px;
  height: 24px;
  background-color: #222222;
  background-image: url(../../images/save1d.svg);
  border: none;
  border-radius: 50px;
  padding: 0;
  cursor: pointer;
}

.card__button_saved {
  background-image: url(../../images/save1.svg);
}

.card__button_delete {
  background-image: url(../../images/d1.svg);
}

.card__button:hover {
  transition: opacity 0.5s linear;
  opacity: 0.5;
}

.card__time {
  border-top: 1px solid #424242;
  color: #8B8B8B;
  font-size: 11px;
  font-weight: 400;
  padding-top: 6px;
}

@media screen and (max-width: 768px) and (min-width: 720px) {
  .card {
      width: 339px;

  }

  .card__img {
      height: 190px;
  }
}


@media screen and (max-width: 720px) and (min-width: 320px) {
  .card {
      width: 300px;
  }

  .card__img {
      height: 168px;
  }
}
