.about-project {
  background: #202020;
  max-width: 1140px;
  margin: auto;
  padding: 110px 70px;
}

.about-project__title {
  color: #FFF;
  font-size: 22px;
  font-weight: 400;
  line-height: 27px;
  border-bottom: 1px solid #DADADA;
  margin: 0;
  padding-bottom: 23px;
  text-align: left;
}

.about-project__conteiner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  gap: 40px;
  margin: 70px 0 110px 0;
  text-align: left;
}

.about-project__info {
  max-width: 550px;

}

.about-project__subtitle {
  color: #FFF;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  margin: 0 0 26px 0;
}

.about-project__text {
  color: #FFF;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
}

.about-project__time {
  display: flex;
  height: 36px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 14px;
}

.about-project__week {
  display: flex;
  align-items: center;
  width: 20%;
  justify-content: center;
  background: #3DDC84;
  color: #000;
}

.about-project__weeks {
  display: flex;
  align-items: center;
  width: 80%;
  justify-content: center;
  background: #303030;
  color: #FFF;
}

.about-project__time-text {
  display: flex;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #8B8B8B;
}

.about-project__backend-text {
  display: flex;
  align-items: center;
  width: 20%;
  justify-content: center;
  color: #8B8B8B;
}

.about-project__frontend-text {
  display: flex;
  align-items: center;
  width: 80%;
  justify-content: center;
  color: #8B8B8B;
}

@media screen and (max-width: 768px) {
  .about-project {
    padding: 90px 50px;
  }

  .about-project__conteiner {
    margin: 70px 0 93px 0;
    gap: 30px;
  }

  .about-project__info {
    max-height: 124px;

  }

  .about-project__text {
    font-size: 12px;
    line-height: 18px;

  }
}

@media screen and (min-width: 320px) and (max-width: 550px) {
  .about-project {
    padding: 70px 18px;
  }

  .about-project__title {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 28px;

  }


  .about-project__conteiner {
    flex-direction: column;
    margin: 60px 0;
    gap: 56px
  }

  .about-project__subtitle {
    font-size: 18px;
    line-height: 22px;
    margin: 0 0 16px 0;
  }

  .about-project__text {
    font-size: 11px;
    line-height: 16px;
    margin: 0;
  }

  .about-project__time {
    font-size: 11px;
    line-height: 13px;
    height: 35px;

    margin-bottom: 10px;
  }

  .about-project__time-text {

    font-size: 11px;
    line-height: 13px;
  }

  .about-project__backend-text {
    width: 35%;
  }

  .about-project__frontend-text {

    width: 65%;
  }

  .about-project__week {
    width: 35%;
  }

  .about-project__weeks {
    width: 65%;
  }
}
