.register {
  padding: 70px 0;
}

.register__container {
  display: flex;
  flex-direction: column;
  max-width: 396px;
  height: 100%;
  margin: 0 auto;
}

.register__title {
  color: #FFF;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 29px;
  margin: 38px 0 40px 0;
}

.register__form {
  height: 266px;
}

.register__button {
  width: 396px;
  height: 45px;
  border-radius: 3px;
  background: #4285F4;
  color: #FFF;
  font-size: 14px;
  font-weight: 500;
  margin: 69px 0 16px 0;
  padding: 0;
  border: none;
  cursor: pointer;
}

.register__button_disabled {
  background: #2F2F2F;
  pointer-events: none;
}

.register__text {
  color: #8B8B8B;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  margin: 0;
}

.register__link {
  color: #4285F4;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  margin-left: 7px;
}

.register__error {
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: brown;
}

@media screen and (max-width: 768px) {
  .register {
      padding: 232px 0 232px 0;
  }

  .register__button {
      margin: 69px 0 16px 0;
  }
}

@media screen and (max-width: 405px) {

  .register {
      padding: 56px 30px 30px 30px;
  }

  .register__container {
      width: 260px;
      margin: 0 auto;
      text-align: center;
  }

  .register__form {
      text-align: left;
  }

  .register__title {
      margin: 50px auto 80px;
  }

  .register__button {
      width: 260px;
      margin: 157px 0 14px;
  }

  .register__text {
      font-size: 12px;
  }

  .register__link {
      font-size: 12px;
  }
}
