.formfield {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0;

}

.formfield__label {
  display: block;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #8B8B8B;
  margin-bottom: 5px;
}

.formfield__input {
  background: #202020;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #FFF;
  border: none;
}

.formfield__error {
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #4285f4;
  margin-top: 10px;
  margin-bottom: 24px;
  border-bottom: 1px solid #424242;
}
