.error {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.error__title {
  color: #FFF;
  text-align: center;
  font-size: 140px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 246px auto 0;
}

.error__subtitle {
  color: #FFF;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 5px auto 184px;
}

.error__link {
  color: #4285F4;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0 auto 60px
}

@media screen and (max-width: 768px) and (min-width: 431px) {
  .error__title {
      margin: 408px auto 0;
  }

  .error__link {
      margin: 0 auto 222px
  }
}

@media screen and (max-width: 430px) {
  .error__title {
      font-size: 80px;
      margin: 329px auto 0;
  }

  .error__subtitle {
      font-size: 12px;
      margin: 10px auto 284px;
  }

  .error__link {
      font-size: 12px;
      margin: 0 auto 30px
  }
}
