.about-me {
  background: #202020;
  max-width: 1140px;
  margin: 0 auto;
  padding: 110px 70px 100px 70px;

}

.about-me__title {
  color: #FFF;
  font-size: 22px;
  font-weight: 400;
  line-height: 27px;
  border-bottom: 1px solid #DADADA;
  margin: 0;
  padding-bottom: 23px;
  text-align: left;
}

.about-me__container {
  display: flex;
  justify-content: space-between;
  padding-top: 66px;
}

.about-me__info {
  text-align: left;
}

.about-me__name {
  color: #FFF;
  font-size: 50px;
  font-weight: 400;
  line-height: 58px;
  margin: 0 0 18px 0;
}

.about-me__job {
  color: #FFF;
  font-size: 18px;
  font-weight: 500;
  line-height: 20px;
  margin: 0 0 26px 0;
}

.about-me__text {
  max-width: 600px;
  color: #FFF;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  margin: 0 0 55px 0;
}

.about-me__link {
  color: #FFF;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  margin: 0;
  text-decoration: none;
  cursor: pointer;
}

.about-me__photo-container {
  width: 270px;
  height: 327px;
  border-radius: 10px;
}

.about-me__photo {
  width: 100%;
  height: 100%;
  background-image: url('../../images/photo.jpeg');
  background-size: cover;
  background-position: center;
  border-radius: inherit;

}

@media screen and (max-width: 1010px) and (min-width: 769px) {
  .about-me__text {
      max-width: 364px;
      font-size: 12px;
      line-height: 18px;
      margin: 0 0 51px 0;
  }

  .about-me__photo-container {
      width: 255px;
      height: 307px;
  }
}

@media screen and (max-width: 768px) {

  .about-me__name {
      font-size: 40px;
      line-height: 40px;
      margin: 0 0 16px 0;
  }

  .about-me__job {
      font-size: 12px;
      line-height: 18px;
      margin: 0 0 20px 0;
  }

  .about-me__text {
      max-width: 364px;
      font-size: 12px;
      line-height: 18px;
      margin: 0 0 51px 0;
  }

  .about-me {
      padding: 90px 50px 90px 50px;
  }

  .about-me__photo-container {
      width: 255px;
      height: 307px;
  }
}

@media screen and (max-width: 730px) and (min-width: 320px) {
  .about-me__container {
      display: flex;
      flex-direction: column-reverse;
      column-gap: 40px;
      padding-top: 60px;
  }

  .about-me__photo-container {
      width: 292px;
      height: 352px;
  }

  .about-me__name {
      margin: 40px 0 20px 0;
  }
}

@media screen and (max-width: 540px) {
  .about-me {
      padding: 70px 14px 0 14px;
  }

  .about-me__title {
      font-size: 18px;
      line-height: 22px;
      padding-bottom: 28px;
  }

  .about-me__name {
      font-size: 30px;
      line-height: 36px;

  }

  .about-me__job {
      font-size: 11px;
      line-height: 16px;
  }

  .about-me__text {
      font-size: 11px;
      line-height: 13px;
      margin: 0 0 22px 0;
  }
}
